<template>
    <div class="layout-content-main">
        <Row style="background: #eee;padding:20px">
            <Col span="11">
                <Card :bordered="false">
                    <p slot="title">System Information</p>
                    <p>系统版本号:</p>
                    <p>系统运行时长:</p>
                    <p>系统时间:</p>
                </Card>
            </Col>
            <Col span="11" offset="2">
                <Card shadow>
                    <p slot="title">Device Information</p>
                    <p>在线:</p>
                    <p>离线:</p>
                    <p>客户端:</p>
                </Card>
            </Col>
        </Row>
        <Row>
            <Col span="11">
                <div id="cpuChart" style="height: 400px;width: 500px"></div>
            </Col>
            <Col span="11" offset="2">
                <div id="memChart" style="height: 400px;width: 500px"></div>
            </Col>
        </Row>
        <Row>
            <Col span="11">
                <div id="devChart" style="height: 400px;width: 500px"></div>
            </Col>
            <Col span="11" offset="2">
                <div id="clientChart" style="height: 400px;width: 500px"></div>
            </Col>
        </Row>
    </div>
</template>
<script>
    console.debug('home page...');
    import echarts from 'echarts';
    export default {
        data () {
            return {}
        },
        mounted(){
            this.drawLine('cpuChart');
            this.drawLine('memChart');
            this.drawLine('devChart');
            this.drawLine('clientChart');
        },
        methods: {
            drawLine(id){
                let myChart = echarts.init(document.getElementById(id));
                myChart.setOption({
                    title: {
                        text: 'CPU使用率',
                        subtext: 'demo',
                        x: 'center'
                    },
                    tooltip: {},
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: ['已使用', '未使用']
                    },
                    series: [{
                        name: '',
                        type: 'pie',
                        radius: '55%',
                        center: ['50%', '60%'],
                        data: [
                            {value: 6, name:'已使用'},
                            {value: 4, name:'未使用'}
                        ],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0,0,0,0.5)'
                            }
                        }
                    }]
                });
            }
        }
    }
</script>


